// mockjs
const Mock = require('mockjs');

const wares=require('./mockjs/wares')  // 
const recommend=require('./mockjs/recommend ')  // 
const users=require('./mockjs/user')  // 


var list=function(options){  // 
    var user=JSON.parse(JSON.stringify(users)).users; //
    var us=JSON.parse(options.body);  //
    for(let i in user){
        if(us.a==user[i].account&&us.m==user[i].password){
            return true
        }
    }
    
    }


Mock.mock('name',list)  //
Mock.mock('wares',wares)  //
Mock.mock('recommend',recommend)  // 






